</template>

<script>
export default {
  name: "FlippableImage",
  props: {
    imageRef: {
      type: String,
      required: true
    },
    article: {
      type: Object,
      required: true
    }
  },
  data: () => {
    return {
      rotateX: false
    };
  },
  methods: {
    getImageUrl(ref) {
      return require('../assets/images/' + ref);
    }
  }

};
